.fullstack-container {
  position: fixed;
}
.fullstack-container .cursor {
  display: none;
  pointer-events: none;
}
@media screen and (min-width: 64rem) {
  .fullstack-container .cursor {
    display: flex;
    position: fixed;
    top: 0;
    left: 0;
    width: 4rem;
    height: 4rem;
    border-radius: 50%;
    z-index: 100;
    border: var(--warna-biru) solid 1px;
  }
}
.fullstack-container .container .video-container {
  position: relative;
  background-color: var(--warna-cream);
}
@media screen and (min-width: 64rem) {
  .fullstack-container .container .video-container {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100vw;
    height: 100vh;
  }
}
.fullstack-container .container .video-container .shadow {
  z-index: 3;
  position: absolute;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.4);
  opacity: 0;
  pointer-events: none;
}
.fullstack-container .container .video-container .video {
  border-radius: 0;
}
@media screen and (min-width: 64rem) {
  .fullstack-container .container .video-container .video {
    border-radius: 1.5rem;
  }
}
.fullstack-container .container .video-container .video video {
  cursor: pointer;
  border-radius: 0rem;
  width: 100vw;
  transition: box-shadow 0.5s ease;
}
@media screen and (min-width: 64rem) {
  .fullstack-container .container .video-container .video video {
    border-radius: 1.5rem;
    width: 80vw;
  }
}
.fullstack-container .container .video-container .video video:hover {
  box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.2);
}
.fullstack-container .container .skill-container {
  position: relative;
  z-index: 3;
  width: 100vw;
  height: 100vh;
  background-color: var(--warna-cream);
}
.fullstack-container .container .project-container .project-list {
  overscroll-behavior: none;
  width: 400% !important;
  height: 100vh;
  display: flex;
  flex-wrap: nowrap;
}
.fullstack-container .container .project-container .project-list .project {
  background-color: var(--warna-cream);
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
}